.main-div {
  width: 100%;
  height: auto;
  float: left;
}
.middle-circle, .left-side {
  float: left;
}
.right-side {
  float: right;
  width: 20%;
  position: relative;
  margin-top: 32px;
}
.right-side .twentyseven-svg, .right-side .coreteam-svg, .right-side .unitteam-svg {
  width: 88%;
  height: 100%;
}
.right-side .scrollerdivchart {
  padding: 0;
  left: 29px;
  position: absolute;
  width: 80%;
  top: 40px;
  height: 507px;
  overflow-y: auto;
  padding-right: 19px;
  scrollbar-color: #d4aa70 #e4e4e4;
  scrollbar-width: thin;
  padding-left: 5px;
}
.right-side .scrollerdivchart .vachart .apexcharts-canvas, .right-side .scrollerdivchart .haciichart .apexcharts-canvas {
  background-image: url("../src/images/chart_grid.svg");
  background-repeat: no-repeat;
  background-position: 0px 11px;
}
.right-side .scrollerdivchart .haciichart {
  padding-bottom: 33px;
  border-bottom: 1px solid #143947;
}
.right-side .scrollerdivchart .haciichart > div {
  min-height: 1px !important;
}
.right-side .scrollerdivchart .left-float {
  float: left;
  color: #1d95c5;
  font-size: 11px;
}
.right-side .scrollerdivchart .right-float {
  float: right;
  text-align: right;
  color: #0eff45;
  font-size: 11px;
}
.right-side .scrollerdivchart .right-float > p:first-child {
  color: #108d38;
  margin-bottom: 5px;
}
.right-side .scrollerdivchart h4 {
  color: #1d95c5;
  font-size: 18px;
  font-weight: 600;
  font-style: italic;
  margin: 0px 0 10px 0;
  border-bottom: 1px solid #15495e;
  padding-bottom: 5px;
  text-align: left;
}
.right-side .scrollerdiv {
  padding: 0;
  left: 50px;
  position: absolute;
  width: 66%;
  top: 40px;
  height: 507px;
  overflow-y: auto;
  padding-right: 19px;
  scrollbar-color: #d4aa70 #e4e4e4;
  scrollbar-width: thin;
  padding-left: 5px;
}
.right-side .scrollerdiv h4 {
  color: #1d95c5;
  font-size: 18px;
  font-weight: 600;
  font-style: italic;
  margin: 0px;
  border-bottom: 1px solid #15495e;
  padding-bottom: 5px;
  text-align: left;
}
.right-side .scrollerdiv ul {
  margin: 7px 0px 30px 0;
  padding: 0px;
}
.right-side .scrollerdiv ul li {
  color: #63828f;
  font-size: 13px;
  font-style: italic;
  text-align: left;
  font-family: "Proxima Nova";
  padding: 0 0 8px 0;
  display: flex;
}
.right-side .scrollerdiv ul li .circle {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 50%;
  background-color: #142b34;
  border: 1px solid #1d95c5;
  margin-right: 5px;
}
.right-side .scrollerdiv ul li .span-txt {
  width: 95%;
}
.right-side .scrollerdiv .strips-section {
  margin: 15px 0 0 0;
}
.right-side .scrollerdiv .strips-section-new {
  background-color: #1b3b48;
  margin: 0px -3px;
  padding: 10px 5px;
}
.right-side .scrollerdiv .strips-section-new p {
  color: #fff;
  font-size: 11px;
  font-weight: 600;
  margin: 0 0 10px 0;
}
.right-side .scrollerdiv .strip-bar {
  background-color: #e75113;
  padding: 7px 5px;
  text-align: center;
  border-radius: 5px;
  color: #ffeaea;
  font-size: 11px;
  display: block;
  font-weight: 600;
  margin-bottom: 10px;
  text-decoration: none;
}
.middle-circle {
  width: 59%;
}
.left-side {
  width: 20%;
  min-width: 20%;
  padding-left: 10px;
}
.right-side-bg {
  background-image: url("../src/images/right-side-part.svg");
  background-repeat: no-repeat;
  position: absolute;
  right: 0;
  top: 0;
}
.left-side-svg {
  width: 100%;
  margin-top: 50%;
}
.LM_logo_bg img {
  width: 20%;
  position: fixed;
  bottom: -0.6%;
  left: -0.5%;
}
.moving-core-bottom img {
  width: 20%;
  position: fixed;
  bottom: -0.6%;
  right: -0.5%;
}
.right-side-bg {
  width: 100%;
  height: 100%;
  position: relative;
  box-sizing: border-box;
  padding: 15px;
  text-align: left;
}
.right-side-bg h3 {
  color: #1d95c5;
  font-size: 24px;
  font-weight: 600;
  font-style: italic;
  border-bottom: 1px solid #15495e;
  margin-bottom: 5px;
  padding-left: 10px;
  padding-bottom: 5px;
}
.right-side-bg .txt {
  color: #63828f;
  font-size: 12px;
  font-weight: 500;
  font-style: italic;
  padding-left: 10px;
}
.right-side-bg .orange-strip {
  background-color: #e75113;
  color: #fff;
  border-radius: 4px;
  width: 80%;
  font-size: 12px;
  padding: 4px;
  margin: 0px auto;
}
.right-side-bg .orange-strips-div {
  text-align: center;
}
.disable-svg .cls-25, .disable-svg .cls-21, .disable-svg .cls-31, .disable-svg .cls-33 {
  fill: #68767c;
}
.selected-stroke {
  stroke: #fff;
  stroke-width: 3px;
}
.cls-25, .cls-21, .cls-31, .cls-33, .cls-28 {
  cursor: pointer;
}
.scrollerdiv::-webkit-scrollbar, .scrollerdivchart::-webkit-scrollbar {
  width: 6px;
}
.scrollerdiv::-webkit-scrollbar-track, .scrollerdivchart::-webkit-scrollbar-track {
  background-color: transparent;
}
.scrollerdiv::-webkit-scrollbar-thumb, .scrollerdivchart::-webkit-scrollbar-thumb {
  border-radius: 0;
  background-color: #15495e;
}
.show{display: block;}
.hide{display:none;}
.middle-circle text{cursor: pointer;}
.loader-img{background-image: url(../src/images/loader.svg);width: 37px; 
  height: 37px;
  position: absolute;
  top: calc(50% - 18px);
  left: calc(50% - 18px);}
.loaderDiv{background-color: rgba(0,0,0,0.1);
  position: absolute;
  left: 3px;
  top: 24px;
  width: 92%;
  height: 100%;}


